<template>
  <el-dialog
    title=""
    v-model="centerDialogVisible1"
    width="30%"
    destroy-on-close
    center
  >
    <strong></strong>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="autoCheckStatus"> </el-button>
        <el-button @click="centerDialogVisible1 = false"> </el-button>
      </span>
    </template>
  </el-dialog>
  <el-dialog title="" v-model="centerDialogVisible2" width="300px">
    <el-form :model="passForm">
      <el-form-item label="" label-width="100px">
        <el-select
          v-model="passForm.isPass"
          placeholder=""
          popper-class="isPassSelect"
        >
          <el-option label="" value="true"></el-option>
          <el-option label="" value="false"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="centerDialogVisible2 = false"> </el-button>
        <el-button type="primary" @click="processPassStatus(passForm.isPass)"
          > </el-button
        >
      </span>
    </template>
  </el-dialog>
  <div class="contentSearch">
    <el-button-group>
      <el-button class="btn-form" icon="el-icon-search" @click="form()"
        ></el-button
      >
      <el-button class="btn-refresh" icon="el-icon-refresh" @click="refresh()"
        ></el-button
      >
      <el-button
        class="btn-save"
        icon="el-icon-document-checked"
        @click="save()"
        ></el-button
      >
      <el-button
        class="btn-finish"
        icon="el-icon-folder-checked"
        @click="finish"
        >{{ finishText }}</el-button
      >
    </el-button-group>
  </div>
</template>

<script>
import * as sysApi from "../../services/sys";
import * as util from "../../services/util";
export default {
  name: "contentSearch",
  data() {
    return {
      passForm: {
        isPass: "true",
      },
      modal: false,
      centerDialogVisible1: false,
      centerDialogVisible2: false,
      cities: [],
      value: "",
      finishText: "",
    };
  },
  mounted() {
    var role = util.isRoleAuth(this.$user);
    if ([1, 3].includes(role)) {
      this.finishText = "";
    }
    if ([2, 3].includes(role)) {
      this.finishText = "";
    }
  },
  methods: {
    form() {
      this.$emit("refreshForm");
    },
    refresh() {
      this.$emit("refresh");
    },
    save() {
      this.$emit("finish");
    },
    finish() {
      if (!window.currentTask) {
        return;
      }
      if (this.finishText == "") {
        this.centerDialogVisible2 = true;
      } else {
        this.centerDialogVisible2 = true;
      }
    },
    finishToProcess(isPass) {
      // 0 1
      if (isPass == "0") {
        this.centerDialogVisible1 = false;
        this.centerDialogVisible2 = false;
        this.$emit("remakeTask");
      } else if (isPass == "1") {
        this.processPassStatus(true);
      }
    },
    processPassStatus(isPass) {
      var $this = this;
      var _checkData = {
        taskId: "" + window.currentTask.id,
        properties: [
          {
            id: "passFlag",
            value: isPass + "",
          },
        ],
      };
      var $url = $this.$config.kts + "form/form-data";
      sysApi
        .postAsyncDatas({
          url: $url,
          data: _checkData,
        })
        .then((res) => {
          console.log(res);
          $this.centerDialogVisible1 = false;
          $this.centerDialogVisible2 = false;
          $this.$emit("remakeTask");
        });
    },
    // ,
    autoCheckStatus() {
      var $this = this;
      var bodyParam = {
        activityId: window.currentTask.formatTags.currentActivity, //ID (tagcurrentActivity
        taskId: window.currentTask.taskId,
      };
      var $url = $this.$config.kts + "dispatch/job/start";
      sysApi
        .postAsyncDatas({
          url: $url,
          data: bodyParam,
        })
        .then((res) => {
          if (res.code != "0") {
            $this.$notify({
              title: "",
              message: ": " + res.message,
              type: "warning",
            });
            $this.centerDialogVisible1 = false;
            $this.centerDialogVisible2 = false;
            return;
          }
          $this.finishToProcess(res.result);
        });
    },
  },
};
</script>

